const userUtils = {
  computed: {
    user: function(){
      return this.$store.getters['getUser']
    },
    userProfilePic: function(){
      if(!this.user) return ''
      return `${process.env.VUE_APP_API_URL}/user/avatar` 
    },
    userNames: function(){
      if(!this.user) return '' 
      return this.user.fursonaDisplayName ? this.user.fursonaDisplayName : this.user.names
    },
    hasTicket: function () {
      if (!this.user) return false
      if (!this.user.ticket) return false
      return true
    },
    hasComplimentaryTicket: function () {
      if (!this.user) return false
      if (!this.user.ticket) return false
      if (this.user.ticket.isComplimentary == true) return true
      return false
    },
    hasRolloverTicket: function () {
      if (!this.user) return false
      if (!this.user.rolloverTicket) return false
      return true
    },
    hasDonatedTicket: function () {
      if (!this.user) return false
      if (!this.user.donatedTicket) return false
      return true
    },
    hasRefundedTicket: function () {
      if (!this.user) return false
      if (!this.user.refundRequest) return false
      return true
    },
    getTicket: function() {
      if (!this.user) return null
      if (this.hasTicket) return this.user.ticket
      if (this.hasRolloverTicket) return this.user.rolloverTicket
      if (this.hasDonatedTicket) return this.user.donatedTicket
      if (this.hasRefundedTicket) return this.user.refundRequest
      return null
    }
  }
}

export default userUtils
