<template>
	<div class="box">
		<div class="hero is-medium" v-if="!fetchDone">
			<div class="hero-body has-text-centered has-text-white">
				{{$t('donations.loading')}}
			</div>
		</div>
		<section class="section" v-else-if="!authenticated && fetchDone">
			<div class="container">
				<CuteBox class="has-text-centered">
					<div class="content">
					<p>{{$t('donations.loginMessage')}}</p>
					</div>
					<div class="buttons is-centered">
					<a :href="makeUrl('/signin','/convencion/ong')" class="button is-medium is-warning">{{$t('donations.loginButton')}}</a>
					</div>
				</CuteBox>
			</div>
		</section>
		<section class="section payday-container" v-else-if="authenticated && fetchDone">
			<div v-if="paymentType == null">
				<h1 class="subtitle is-5 has-text-centered">{{$t('donations.step')}} 1</h1>
				<h1 class="title is-3 has-text-centered">{{$t('donations.stepOneTitle')}}</h1>
				<div class="columns is-centered mt-5">
					<div class="column is-narrow">
						<a @click="changePaymentType('MP')"><img src="https://new.furfiesta.com/wp-content/uploads/2021/10/697ab8773d917d5efba30cc803ea1ce66d74e227.png" class="image" width="200" alt=""></a>
					</div>
					<div class="column is-narrow is-offset-1">
						<a @click="changePaymentType('PP')"><img src="https://new.furfiesta.com/wp-content/uploads/2021/10/740c1425f49b39643947f1f4964ff75d5bd885e8.png" class="image" width="200" alt=""></a>
					</div>
				</div>
			</div>
			<div v-if="paymentType && doneSelecting == null">
				<a href="#" @click="paymentType = null" class="has-text-info goback">⬅️ {{$t('donations.changeMethodButton')}}</a>
				<h1 class="subtitle is-5 has-text-centered">{{$t('donations.step')}} 2</h1>
				<h1 class="title is-3 has-text-centered">{{$t('donations.stepTwoTitle')}}</h1>
				<div class="my-6">
				<b-field>
					<b-slider v-model="payday" size="is-large" ticks type="is-info" :step="stepSlider" indicator :tooltip="false" :min="minSlider" :max="maxSlider"></b-slider>
        </b-field>
				</div>
				<h1 class="subtitle is-3 has-text-centered">{{$t('donations.donationQuantity')}} {{payday}}&nbsp;{{paymentType == 'MP' ? 'AR$' : 'USD'}}</h1>
				<h1 class="title is-4 has-text-centered">{{$t('donations.readyToDonate')}}</h1>
				<div class="buttons is-centered">
					<button @click="startPayment" class="button is-large is-info">{{$t('donations.goTo',{method: paymentType == 'MP' ? 'Mercado Pago' : 'PayPal'})}}</button>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
import userUtils from '../mixins/userUtils'
export default {
	data(){
		return {
			fetchDone: false,
			authenticated: false,
			//
			paymentType: null,
			doneSelecting: null,
			payday: 0
		}
	},
	mixins: [userUtils],
	mounted: function () {
		// REQUIRED
		this.fetchDone = false
		this.$http.get('/user')
		.then(response => {
			this.$store.commit('updateUser', response.data)
			this.authenticated = true
		})
		.catch(() => {})
		.finally(() => {
			this.fetchDone = true
			// console.error(errUser)
		})
	},
	computed: {
		minSlider: function(){
			if(!this.paymentType) return 100
			if(this.paymentType == 'MP') return 100
			if(this.paymentType == 'PP') return 5
			return 100
		},
		maxSlider: function(){
			if(!this.paymentType) return 10000
			if(this.paymentType == 'MP') return 10000
			if(this.paymentType == 'PP') return 100
			return 10000
		},
		stepSlider: function(){
			if(!this.paymentType) return 100
			if(this.paymentType == 'MP') return 100
			if(this.paymentType == 'PP') return 5
			return 100
		}
	},
	methods: {
		changePaymentType: function(type){
			this.paymentType = type
			this.payday = 0
		},
		startPayment: async function () {
			if(this.paymentType == 'MP') {
				this.checkoutMercadoPago()
			} else {
					await this.$swal.fire({
						title: this.isSpanish ? 'ESTO ES MUY IMPORTANTE' : 'THIS IS VERY IMPORTANT',
						icon: 'warning',
						text: this.isSpanish ? 'Cuando finalices tu pago, es importante que hagas clic en el botón "VOLVER AL VENDEDOR" para que tu pago se acredite instantaneamente' : 'After you\'ve done the checkout, its important that you click the button "GO TO MERCHANT" so your payment is instantaneously accredited',
						showCancelButton: true,
						cancelButtonText: this.isSpanish ? 'Cancelar' : 'Cancel',
						allowOutsideClick: false,
						allowEscapeKey: false,
						allowEnterKey: false,
						confirmButtonText: this.isSpanish ? '¡OK! ¡Entendido!' : 'OK! I will do it!',
					didOpen: () => {
						this.$swal.showLoading()
					setTimeout(() => {
						this.$swal.hideLoading()
						},5000)
					}
				}).then((res) => {
					if (res.isConfirmed){
						this.checkoutPaypal()
					}
					if (res.isDismissed) {
						this.closeModalPayment()
					}
				})
			}
		},
		checkoutMercadoPago: function () {
			this.$swal.fire({
				title: this.isSpanish ? 'Redirigiendote a Mercado Pago' : 'Redirecting you to Mercado Pago',
				text: this.isSpanish ? 'Tu navegador te llevará automaticamente  a Mercado Pago' : 'Your browser will automatically take you to Mercado Pago',
				icon: 'info',
				timer: 20000,
				timerProgressBar: true,
				showConfirmButton: false,
				didOpen: () => {
					this.$http.post("https://api.furfiesta.com/charity/checkout",{
						currency: 'ARS',
						amount: this.payday
					}).then((response) => {
						location.href = 'https://api.furfiesta.com' + response.data.redirectURL
					}).catch( err => {
						console.error(err)
					})
				},
			}).then((result) => {
				/* Read more about handling dismissals below */
				if (result.dismiss === this.$swal.DismissReason.timer) {
					console.log('I was closed by the timer')
				}
				this.closeModalPayment()
				return
			})
		},
		checkoutPaypal: function() {
		if(this.readOnly) return
			this.$swal.fire({
				title: this.isSpanish ? 'Redirigiendote a PayPal' : 'Redirecting you to PayPal',
				text: this.isSpanish ? 'Tu navegador te llevará automaticamente a PayPal' : 'Your browser will automatically take you to PayPal',
				icon: 'info',
				timer: 20000,
				timerProgressBar: true,
				showConfirmButton: false,
				didOpen: () => {
					this.$http.post("https://api.furfiesta.com/charity/checkout",{
						currency: 'USD',
						amount: this.payday
					}).then((response) => {
						location.href = 'https://api.furfiesta.com' + response.data.redirectURL
					}).catch( err => {
						console.error(err)
					})
				},
			}).then((result) => {
				/* Read more about handling dismissals below */
				if (result.dismiss === this.$swal.DismissReason.timer) {
					console.log('I was closed by the timer')
				}
				this.closeModalPayment()
				return
			})
		},
	}
}
</script>

<style lang="scss" scoped>
.goback{
	position: absolute;
	top: 0;
	left: 0;
}
.payday-container{
	position:relative;
}
</style>