import Vue from 'vue'
import VueI18n from 'vue-i18n'
import messages from './i18n'
import Buefy from 'buefy'
import http from './http'
import store from './store'
import VueSweetalert2 from 'vue-sweetalert2';
import baseUtils from './mixins/baseUtils'
// Views
import Auth from './views/Auth.vue'
import Donations from './views/Donations.vue'
import RecoverPassword from './views/RecoverPassword.vue'
import Profile from './views/Profile.vue'
import CTF from './views/CTF.vue'

Vue.use(Buefy, {
  defaultIconPack: 'fas',
  defaultDialogConfirmText: 'OK',
  defaultDialogCancelText: 'Cancelar',
  defaultDayNames: ['Dom/Sun', 'Lun/Mon', 'Mar/Tue', 'Mie/Wed', 'Jue/Thu', 'Vie/Fri', 'Sab/Sat'],
  defaultMonthNames: ['Enero/January', 'Febrero/February', 'Marzo/March', 'Abril/April', 'Mayo/May', 'Junio/June', 'Julio/July', 'Agosto/August', 'Septiembre/September', 'Octubre/October', 'Noviembre/November', 'Diciembre/December']
})
Vue.use(VueI18n)
Vue.use(VueSweetalert2);
Vue.mixin(baseUtils)

Vue.prototype.$http = http
Vue.config.productionTip = false


// Create VueI18n instance with options
const i18n = new VueI18n({
  locale: window.location.pathname.includes('/en/') != 0 ? 'es' : 'en', // set locale
  fallbackLocale: 'es',
  messages, // set locale messages
})

// let bypassMount = false
// if(process.env.NODE_ENV == 'development'){
//   bypassMount = true
// }
// console.log(bypassMount)

const needsLogin = (component, id) => {
  http.get('/user/loggedIn').then( response => {
  if(response.data.loggedIn){
    return http.get('/user')
  }
    throw Error ('Needs to be logged in')
  })
  .then(response => {
    store.commit('updateUser', response.data)
    new Vue({
      i18n,
      store,
      http,
      render: h => h(component),
    }).$mount(`#${id}`)
    return
  })
  .catch(err => {
    console.log(err)
    const redirectUrl = window.location.pathname
    window.location.replace(`/signin?redirect=${redirectUrl}`);
  })  
}

if(window.location.pathname.indexOf('/signin') >= 0){
  console.log('Mounting #vue-auth..')
  new Vue({
    i18n,
    store,
    http,
    render: h => h(Auth),
  }).$mount('#vue-auth')
}

if(window.location.pathname.indexOf('/recover-password') >= 0){
  console.log('Mounting #vue-recover..')
  new Vue({
    i18n,
    store,
    http,
    render: h => h(RecoverPassword),
  }).$mount('#vue-recover')
}
if(window.location.pathname.indexOf('/convencion/ong') >= 0){
  console.log('Mounting #vue-donations-ong..')
  new Vue({
    i18n,
    store,
    http,
    render: h => h(Donations),
  }).$mount('#vue-donations-ong')
}

if(window.location.pathname.indexOf('/neptuno') >= 0){
  console.log('Mounting #vue-ctf..')
  new Vue({
    i18n,
    store,
    http,
    render: h => h(CTF),
  }).$mount('#vue-ctf')
}

if(window.location.pathname.indexOf('/profile') >= 0) {
  console.log('Mounting #vue-profile..')
  needsLogin(Profile,'vue-profile')
}


