<template>
	<div class="">
		<div class="hero is-medium" v-if="!fetchDone">
			<div class="hero-body has-text-centered has-text-white">
				{{$t('ctf.loading')}}.
			</div>
		</div>
		<section class="section" v-else-if="!authenticated && fetchDone">
			<div class="container">
				<CuteBox class="has-text-centered">
					<div class="content">
					<p>{{$t('ctf.loginMessage')}}</p>
					</div>
					<div class="buttons is-centered">
					<a :href="makeUrl('/signin','/ctf')" class="button is-medium is-warning">{{$t('ctf.loginButton')}}</a>
					</div>
				</CuteBox>
			</div>
		</section>
		<section class="section" v-else-if="authenticated && fetchDone">
			<div class="hero is-medium" v-if="!acceptedTerms">
				<div class="hero-body">
					<Glitch class="font-creepy is-size-1 has-text-centered" :text="$t(`ctf.creepyHello[${Math.floor(Math.random() * 8)}]`)"></Glitch>
					<div class="buttons is-centered">
						<a @click="acceptTerms" class="button is-warning filter-button is-uppercase is-small has-text-weight-bold" >{{$t('ctf.acceptTerms')}}</a>
					</div>
				</div>
			</div>
			<section v-else>
				<CuteBox>
					<div class="columns is-vcentered">
						<div class="column is-narrow">
									<img :src="`https://api.furfiesta.com/user/avatar?${givemedate}`" class="image player-avatar" onerror="this.onerror=null;this.src='https://api.furfiesta.com/default_avatar.jpg';" />
						</div>
						<div class="column has-text-centered-touch">
							<h1 class="font-creepy is-size-4 is-400" >{{$t('ctf.whoIsPlaying')}}</h1>
							<h1 class="title is-2">{{userNames}}</h1>
						</div>
						<div class="column has-text-centered-touch has-text-right-desktop">
							<Glitch class="font-pixel is-size-1 " :text="ctfpoints"></Glitch>
							<h1 class="font-pixel is-size-4" >pts</h1>
						</div>
					</div>
				</CuteBox>
				<CuteBox v-if="getTicket.tier != 'staff'">
					<h1 class="is-size-3 has-text-centered font-creepy" >{{$t('ctf.tellUs',{name: userNames})}}</h1>
					<Glitch class="title is-1 has-text-centered" :text="$t('ctf.whatsTheFlag')"/>
					<div class="field has-addons" style="margin-bottom: 10px;">
						<p class="control" style="margin-left: auto">
							<a class="button is-static has-background-dark has-text-warning is-family-monospace is-large" style="padding-left: 10px; padding-right: 10px;">
								ARFF{
							</a>
						</p>
						<div class="control">
							<input type="text" v-model="inputFlag"  @keyup.enter="sendFlag" :disabled="sendingFlag" class="input is-large is-family-monospace has-text-danger has-text-centered has-background-light" style="font-weight: 800;" placeholder="...">
						</div>
						<p class="control" style="margin-right: auto">
							<a class="button is-static has-background-dark has-text-warning is-family-monospace is-large" style="padding-left: 10px; padding-right: 10px;">
								}
							</a>
						</p>
					</div>
					<div class="buttons is-centered">
						<a @click="sendFlag" class="button is-warning is-medium has-text-weight-bold" :class="{'is-loading': sendingFlag}">{{$t('ctf.tryYourLuck')}}</a>
					</div>
				</CuteBox>
				<br>
				<h1 class="title is-1 has-text-centered">Challenges</h1>
				<p class="has-text-centered has-text-white">{{ $t('ctf.filterByCategory') }}<p>
					<br>
					<div class="buttons is-centered">
						<a v-for="(category,i) in categoryList" :key="`category-${i}`" class="button is-warning filter-button is-uppercase is-small has-text-weight-bold" @click="toggleCategory(category)" :class="isCategorySelected(category)">{{category}}</a>
					</div>
				<br>
				<div class="card-deck">
					<Challenge :challenge="challenge" :user="user" v-for="(challenge,i) in sortedChallenges" :key="`challenge-${i}`"/>
				</div>
			</section>
		</section>
		<Leaderboard />
	</div>
</template>

<script>
import CuteBox from '../components/ctf/CuteBox.vue'
import Leaderboard from '../components/ctf/Leaderboard.vue'
import Challenge from '../components/ctf/Challenge.vue'
import Glitch from '../components/ctf/Glitch.vue'
import userUtils from '../mixins/userUtils'

export default {
	data() {
		return {
			givemedate: new Date() / 1,
			//-----------
			acceptedTerms: false,
			fetchDone: false,
			authenticated: false,
			ctfpoints: 0,
			arrChallenges: [],
			inputFlag: null,
			sendingFlag: false,
			idIntervalChallenges: null,
			idIntervalScore: null,
			categoriesSelected: []
		}
	},
	mixins: [userUtils],
	components: {
		CuteBox,
		Leaderboard,
		Challenge,
		Glitch
	},
	mounted: function () {
		// REQUIRED
		this.$http.get('/user')
		.then(response => {
			this.$store.commit('updateUser', response.data)
			return Promise.all([
				this.$http.get('/ctf/score'),
				this.$http.get('/ctf/challenges')
			])
		})
		.then(responses => {
			this.ctfpoints = responses[0].data
			this.arrChallenges = responses[1].data 
			this.idIntervalScore = setInterval(this.refreshScore, 15000);
			this.idIntervalChallenges = setInterval(this.refreshChallenges, 15000);
		})
		.then( () => {
			this.authenticated = true
			this.toggleAllCategories();
		})
		.catch(() => {})
		.finally(() => {
			this.fetchDone = true
			// console.error(errUser)
		})
	},
	computed: {
		sortedChallenges: function() {
			if(!this.arrChallenges.length) return []
			let arr = this.arrChallenges.map(c => {
				let aux = Object.assign({},c)
				if(c.solves.includes(this.user._id)){
					aux.actualPoints = 0
				} else {
					aux.actualPoints = Math.floor(c.points * Math.pow(0.75, c.solves.length)) > 1 ? Math.floor(c.points * Math.pow(0.75, c.solves.length)) : 1
				}
				return aux
			})
			return arr.sort( (a,b) => b.actualPoints - a.actualPoints).filter( a => this.categoriesSelected.includes(a.category))
		},
		categoryList: function(){
			if(!this.arrChallenges.length) return []
			let onlyUniques = function(value, index, self){
		return self.indexOf(value) === index        
			}
			return this.arrChallenges.map(c => c.category).filter(onlyUniques)
		}
	},
	beforeDestroy: function(){
		clearInterval(this.idIntervalChallenges);
		clearInterval(this.idIntervalScore);
	},
	methods: {
		acceptTerms: function(){
			let ran = Math.floor(Math.random() * 3)
			let url = 'https://new.furfiesta.com/wp-content/uploads/2021/05/87dc477ba40aafde873221345902142e5439f5f9.flac'
			if(ran > 1){
				url = 'https://new.furfiesta.com/wp-content/uploads/2021/10/8fb8015cce881dedfb0be942e2c60b670f1bdff4.mp3'
			}
			let audioThingy = new Audio(url);
			audioThingy.volume = 0.7;
			audioThingy.play();
			this.acceptedTerms = true
		},
		restartAll: function () {
			// From here, add whatever you want
			// ---
			this.acceptedTerms = false
			this.fetchDone = false
			this.authenticated = false
			this.ctfpoints = 0
			this.arrChallenges = []
			this.inputFlag = null
			this.sendingFlag = false
			this.idIntervalChallenges = null
			this.idIntervalScore = null
			this.categoriesSelected = null
		},
		refreshChallenges: function(){
			console.log('[CTF] [Challenges] Refreshing challenges...')
			this.$http.get('/ctf/challenges')
			.then( res => {
				this.arrChallenges = res.data
			})
			.catch((errUser) => {
				console.error(errUser)
			});
		},
		refreshScore: function(){
			console.log('[CTF] [Score] Refreshing score...')
			this.$http.get('/ctf/score')
			.then( res => {
				this.ctfpoints = res.data
			})
			.catch((errUser) => {
				console.error(errUser)
				clearInterval(this.idIntervalChallenges);
				clearInterval(this.idIntervalScore);
				this.restartAll()
			});
		},
		sendFlag: function(){
			if(!this.inputFlag) return
			this.sendingFlag = true
			this.$http.post('/ctf/solve', {
				flag: 'ARFF{' + this.inputFlag + '}'
			}, {
				withCredentials: true
			})
				.then((res) => {
					this.inputFlag = null;
					this.refreshChallenges();
					// setTimeout(this.refreshLeaderboard, 1500);
					this.refreshScore();
					(new Audio('https://new.furfiesta.com/wp-content/uploads/2021/04/78cca3ae453d88736b04287a3eb6ab080fbbf39e.mp3')).play();
					this.$swal.fire({
						icon: 'success',
						title: this.$t('ctf.success.title'),
						text: this.$t('ctf.success.text', {points: res.data.points, ctfpoints: res.data.ctfpoints})
					})
				})
				.catch((err) => {
					(new Audio('https://new.furfiesta.com/wp-content/uploads/2021/04/9d28b01d69e953a02ad5b263a57b09bfdbdb3ad2.mp3')).play();
					if(err.response.data.message == 'you have already solved this challenge'){
						this.$swal.fire({
							icon: 'error',
							title: this.$t('ctf.alreadyDone.title'),
							text: this.$t('ctf.alreadyDone.text')
						})
					} else if(err.response.data.message == 'invalid flag'){
						this.$swal.fire({
							icon: 'error',
							title: this.$t('ctf.invalid.title'),
							text: this.$t('ctf.invalid.text')
						})
					}
				})
				.finally( () => {
					this.sendingFlag = false
				})
		},
		toggleAllCategories: function() {
			if(!this.arrChallenges.length) return []
			let onlyUniques = function(value, index, self){
		return self.indexOf(value) === index        
			}
			this.categoriesSelected = this.arrChallenges.map(c => c.category).filter(onlyUniques)
		},
		toggleCategory: function(cat){
			if(this.categoriesSelected.includes(cat)){
				let index = this.categoriesSelected.indexOf(cat)
				this.categoriesSelected.splice(index, 1)
			} else {
				this.categoriesSelected.push(cat) 
			}
		},
		isCategorySelected: function(cat){
			if(this.categoriesSelected.includes(cat)) return ''
				else return 'is-outlined'
		}
	}
}
</script>

