<template>
 <section>
    <img src="https://new.furfiesta.com/wp-content/uploads/2020/11/arff-vertical.svg" class="image" width="200" style="margin: 0 auto 30px">      
    <div class="box" v-if="!successRecovery">
      <h1 class="title">{{$t('recoverPassword.title')}}</h1>
      <div class="field">
        <p class="control">
          <input class="input" v-model="password" type="password" :placeholder="$t('recoverPassword.placeholderPassword')">
        </p>
      </div>
      <div class="field">
        <p class="control">
          <input class="input" v-model="repeatPassword" type="password" :placeholder="$t('recoverPassword.placerholderRepeatPassword')">
        </p>
      </div>
      <div class="buttons is-right">
        <button @click="doRecovery" class="button is-primary" :disabled="recoverLoading" :class="{'is-loading': recoverLoading}">
          {{$t('recoverPassword.actionButton')}}
        </button>
      </div>
    </div>
    <div v-else>
      <p class="title is-3 has-text-success has-text-centered">{{$t('recoverPassword.success.title')}}</p>
      <p class="subtitle is-5 has-text-centered">{{$t('recoverPassword.success.subtitle')}}</p>
      <div class="buttons is-centered">
        <a :href="makeUrl('/')" class="button is-primary">{{$t('login.goHome')}}</a>
      </div>
    </div>
 </section>
</template>

<script>
export default {
  data(){
    return {
      password: null,
      repeatPassword: null,
      recoverLoading: false,
      successRecovery: false,
    }
  },
  computed: {
    theToken: function(){
      return this.getParameterByName('token') 
    }
  },
  methods: {
    doRecovery () {
      if(!this.password || !this.repeatPassword) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('recoverPassword.errors.allFields')
        })
        return
      }
      if (this.password.trim().length < 6) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('recoverPassword.errors.passwordLengthMin')
        })
        return false
      }

      if (this.password != this.repeatPassword) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('recoverPassword.errors.passwordDontMatch')
        })
        return false
      }

      if(!this.theToken) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('recoverPassword.errors.tokenRequired')
        })
        return
      }
      this.recoverLoading = true
      this.recoverFailed = false
      this.recoverSuccess = false
      this.$http.post('/password/reset', {
        password: this.password,
        token: this.theToken
      })
      .then( () => {
        this.$swal.fire({
            icon: 'success',
            title: this.$t('recoverPassword.success.title'),
            text: this.$t('recoverPassword.success.subtitle')
        })
        this.successRecovery = true
      })
      .catch( () => {
        this.$swal.fire({
            icon: 'error',
            title: this.$t('recoverPassword.fail.title'),
            text: this.$t('recoverPassword.fail.subtitle')
          })
      }) 
      .finally(() => {
        this.recoverLoading = false 
      })
    },
    getParameterByName(name, url = window.location.href) {
      // eslint-disable-next-line no-useless-escape
      name = name.replace(/[\[\]]/g, '\\$&');
      var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
      results = regex.exec(url);
      if (!results) return null;
      if (!results[2]) return '';
      return decodeURIComponent(results[2].replace(/\+/g, ' '));
    }
  }
}
</script>