<template>
<div>
  <h1 class="title">
    {{$t('requestRecover.title')}}
  </h1>
  <div class="field">
    <p class="control">
      <label class="label">{{$t('requestRecover.enterEmail')}}&nbsp;<span class="has-text-danger is-size-7">* {{$t('requestRecover.required')}}</span></label>
      <input class="input" v-model="userEmailRecover" v-on:keyup.enter="$refs.captchaRecover.click()" type="email" :placeholder="$t('requestRecover.placeholderEmail')">
    </p>
  </div>
  <div class="buttons is-right">
    <a @click="doShowLogin" class="button is-white">{{$t('requestRecover.goBack')}}</a>
    <Captcha ref="captchaRecover" sitekey="6Lf4T9IaAAAAAE2FUUkD39CTR9-rmTOomRStaqcq" :validate="checkRecoverPassword" :callback="doRecoverPassword"
    class="button is-primary" :class="{'is-loading': recoverEmailLoading}" type="submit" id="register-button" :disabled="recoverEmailLoading">
    {{$t('requestRecover.recoverButton')}}</Captcha>
  </div>
</div>
</template>

<script>
import Captcha from './Captcha.vue'
export default {
  data() {
    return {
      userEmailRecover: null,
      recoverEmailLoading: false,
    }
  },
  components: {
    Captcha
  },
  computed: {
    isSpanish: function () {
      return window.location.pathname.indexOf('/en/') != 0;
    }
  },
  methods: {
    doShowLogin: function () {
      this.$emit('showLogIn')
    },
    checkRecoverPassword: function () {
      const valueIsRequired = (val) => val != null;
      const valueIsNotEmptyString = (val) => val != '';
      const isValidEmail = (val) => {
        const re = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,253}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,253}[a-zA-Z0-9])?)*$/
        return re.test(val)
      }

      let theValues = [
        this.userEmailRecover.toLowerCase().trim()
      ]

      if (!theValues.every(valueIsRequired) || !theValues.every(valueIsNotEmptyString)) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('requestRecover.errors.allFields')
        })
        return false
      }

      if (!isValidEmail(this.userEmailRecover.toLowerCase().trim())) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('requestRecover.errors.emailNotValid')
        })
        return false
      }

      return true
    },
    doRecoverPassword: function (recaptchaToken) {
      if (!recaptchaToken) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('requestRecover.alertRecaptchaTextFail')
        })
        return
      }
      // REQUIRED
      this.recoverEmailLoading = true
      this.$http.post('/password/request', {
        language: this.isSpanish ? 'es' : 'en',
        email: this.userEmailRecover.toLowerCase().trim(),
        captcha: recaptchaToken
      })
        .then(() => {
          this.$emit('successRequestRecover', this.userEmailRecover.toLowerCase().trim())
          this.$swal.fire({
            icon: 'success',
            title: this.$t('requestRecover.recoverSentTitle'),
            text: this.$t('requestRecover.textRequestRecover', {userEmail: this.userEmailRecover.toLowerCase().toLowerCase().trim()})
          })
        })
        .catch(() => {
          this.$swal.fire({
            icon: 'error',
            title: 'Woops...',
            text: this.$t('requestRecover.thereWasAnError')
          })
        })
        .finally(() => {
          this.recoverEmailLoading = false
        });
    },
  }
}
</script>