<template>
  <section>
    <img :src="`https://api.furfiesta.com/user/avatar?${givemedate}`" class="image avatar-frame" width="200" onerror="this.onerror=null;this.src='https://api.furfiesta.com/default_avatar.jpg';" />
    <h1 class="title is-2 has-text-centered">{{$t('profile.hello', {userNames: userNames})}}</h1>
    <div class="buttons is-centered">
      <button class="button is-primary" @click="doLogout">
        Logout
      </button>
    </div>
    <div class="box" v-if="user">
      <h1 class="title is-4">{{$t('profile.aboutYou')}}</h1>
      <div class="columns">
        <div class="column">
          <div class="field">
            <label class="label">{{$t('profile.labelName')}}</label>
            <div class="control">
              <input class="input" disabled readonly type="text" :value="user.names">
            </div>
          </div>
        </div>
        <div class="column">
          <div class="field">
            <label class="label">{{$t('profile.labelSurname')}}</label>
            <div class="control">
              <input class="input" disabled readonly type="text" :value="user.surnames">
            </div>
          </div>
        </div>
        <div class="column">
          <div class="field">
            <label class="label">{{$t('profile.labelCountry')}}</label>
            <div class="control">
              <input class="input" disabled readonly type="text" :value="$t(`countries.${user.country}`)">
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="columns is-multiline">
      <div class="column is-12-touch">
        <div class="box">
          <h1 class="subtitle is-4">{{$t('profile.step1.title')}}</h1>
          <h1 class="title is-4">{{$t('profile.step1.subtitle')}}</h1>
          <div class="content">
          <p>{{$t('profile.step1.text1')}}</p>
          </div>
          <form>
             <div class="field">
              <label class="label">{{$t('profile.step1.labelCompleteNameFursona')}}</label>
              <div class="control">
                <input class="input" v-model="fursonaFullname" required type="text" :placeholder="$t('profile.typeHere')">
              </div>
            </div>
             <div class="field">
              <label class="label">{{$t('profile.step1.labelNameForBadge')}}</label>
              <div class="control">
                <input class="input" v-model="fursonaDisplayName" required type="text" :placeholder="$t('profile.typeHere')">
              </div>
            </div>
             <div class="field">
              <label class="label">{{$t('profile.step1.labelSpecie')}}</label>
              <div class="control">
                <input class="input" v-model="fursonaSpecies" required type="text" :placeholder="$t('profile.typeHere')">
                <p class="help">{{$t('profile.step1.helpSpecie')}}</p>
              </div>
            </div>
            <div class="field">
              <label class="label">{{$t('profile.step1.labelPronouns')}}</label>
              <div class="control">
                <input class="input" v-model="fursonaPronoun" required type="text" :placeholder="$t('profile.typeHere')">
                <p class="help">{{$t('profile.step1.helpPronouns')}}</p>
              </div>
            </div>
            <div class="field">
              <button @click.prevent="sendFursonaData" class="button is-primary" :disabled="isSendingFursonaData" :class="{'is-loading': isSendingFursonaData}">{{$t('profile.saveButton')}}</button>
            </div>
          </form>
        </div>
      </div>
      <div class="column por-dios-librame-de-esto is-12-touch is-narrow-desktop">
        <div class="box has-text-centered">
          <h1 class="subtitle is-4">{{$t('profile.step2.title')}}</h1>
          <h1 class="title is-4">{{$t('profile.step2.subtitle')}}</h1>
          <p>{{$t('profile.step2.help')}}</p>
          <br>
           <Cropper :src="furryAvatarBase" ref="cropper" class="my-croppi" :canvas="theCanvas" :stencil-props="theStencil" @error="hasImageLoaded = false" @ready="hasImageLoaded = true"/>
          <br>
          <div class="field">
            <button @click="$refs.avatarInput.click()"  class="button is-primary">{{ $t('profile.step2.changeAvatarButton') }}
              <input type="file" ref="avatarInput" @change="uploadImage($event)" accept="image/jpg,image/jpeg,image/png" style="display:none"/>
            </button>
          </div>
          <div class="field">
            <button @click="sendProfilePicture" :disabled="!hasImageLoaded" :class="{'is-loading': isSendingFursonaAvatar}" class="button is-primary">{{$t('profile.saveButton')}}</button>
          </div>
        </div>
        </div>
          </div>
  </section>
</template>

<script>
import { Cropper } from 'vue-advanced-cropper';
import 'vue-advanced-cropper/dist/style.css';

// import requiresLogin from '../mixins/requiresLogin'
import userUtils from '../mixins/userUtils'
export default {
  data() {
    return {
      initialized: false,
      givemedate: new Date() / 1,
      //croppa: null,
      furryAvatar: 'https://api.furfiesta.com/user/avatar?' + (new Date() / 1),
      furryAvatarBase: null,
      //-----------
      fursonaFullname: null,
      fursonaDisplayName: null,
      fursonaSpecies: null,
      fursonaPronoun: null,
      fursonaAvatar: null,
      hasImageLoaded: false,
      isSendingFursonaData: false,
      isSendingFursonaAvatar: false,
      theCanvas: {
        minHeight: 900,
        maxHeight: 900,
        maxWidth: 900,
        minWidth: 900
      },
      theStencil: { 
        aspectRatio: 1/1
      }
    }
  },
  components: {
		Cropper
	},
  mixins: [userUtils],
  beforeMount: function(){
    this.fillData()
    this.initialized =  true
  },
  methods: {
    fillData(){
      this.fursonaFullname = this.user.fursonaFullname
      this.fursonaDisplayName = this.user.fursonaDisplayName
      this.fursonaSpecies = this.user.fursonaSpecies
      this.fursonaPronoun = this.user.fursonaPronoun
      this.givemedate = new Date() / 1
      this.refreshCropper()
    },
    refreshCropper: function () {
      this.$http.get(this.furryAvatar, {
        responseType: 'blob'
      })
      .then((res) => {
        let reader = new FileReader();
        reader.onload = () => this.furryAvatarBase = reader.result
        reader.readAsDataURL(res.data)
      })
      .catch((err) => {
        console.error(err)
      });
    },
    doLogout: function () {
      // REQUIRED
      this.$http.post('/user/logout')
        .then(() => {
          window.location.href = '/';
        })
        .catch((err) => {
          console.error(err)
          this.$swal.fire({
            icon: 'error',
            title: 'Oops..',
            text: 'Something happened'
          })
        })
    },
    updateUser: function () {
      // REQUIRED
      this.$http.get('/user')
        .then((response) => {
          this.$store.commit('updateUser', response.data)
          this.fillData()
        })
        .catch((err) => {
          console.error(err)
          this.$swal.fire({
            icon: 'error',
            title: 'Oops..',
            text: 'Something happened'
          })
        });
    },
    uploadImage(event) {
      // Reference to the DOM input element
      var input = event.target;
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader();
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.furryAvatarBase = e.target.result;
        };
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0]);
      }
    },
    sendProfilePicture() {
      this.isSendingFursonaAvatar = true
      const result = this.$refs.cropper.getResult();
      const payload = {
        fursonaAvatar: result.canvas.toDataURL("image/jpeg")
      };
      this.$http.post("https://api.furfiesta.com/user/update", payload, {
        withCredentials: true
      })
        .then(() => {
          this.updateUser()
          this.refreshCropper()
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.step2.avatarSuccess.title'),
            text: this.$t('profile.step2.avatarSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.step2.avatarError.title'),
            text: this.$t('profile.step2.avatarError.text')
          })
        })
        .finally(() => {
          this.isSendingFursonaAvatar = false;
        });
    },
    onImageRemove() {
      this.imageUploaded = false
    },
    onNewImage() {
      this.imageUploaded = true
    },
    sendFursonaData() {
      this.isSendingFursonaData = true;
      const payload = {
        fursonaFullname: this.fursonaFullname,
        fursonaDisplayName: this.fursonaDisplayName,
        fursonaSpecies: this.fursonaSpecies,
        fursonaPronoun: this.fursonaPronoun
      };
      this.$http.post("/user/update", payload)
        .then(() => {
          this.updateUser()
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.step1.alertSuccess.title'),
            text: this.$t('profile.step1.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.step1.alertError.title'),
            text: this.$t('profile.step1.alertError.text')
          })
        })
        .finally(() => {
          this.isSendingFursonaData = false;
        });
    }
  }

}
</script>

