<template>
  <section v-if="initialized">
    <img src="https://new.furfiesta.com/wp-content/uploads/2020/11/arff-vertical.svg" class="image" width="200" style="margin: 0 auto 30px">
    <div v-if="loggedIn">
      <p class="title is-3 has-text-success has-text-centered">{{$t('login.success.title')}}</p>
      <p class="subtitle is-5 has-text-centered">{{$t('login.success.subtitle')}}</p>
      <div class="buttons is-centered">
        <a :href="makeUrl('/')" class="button is-primary">{{$t('login.goHome')}}</a>
      </div>
    </div>
    <div v-else-if="showSuccessSignUp">
      <p class="title is-3 has-text-success has-text-centered">{{$t('signup.alertSuccessTitle')}}</p>
      <p class="subtitle is-5 has-text-centered">{{$t('signup.textSignUpSuccess',{userEmail: emailSignUp})}}</p>
      <div class="buttons is-centered">
        <a :href="makeUrl('/')" class="button is-primary">{{$t('login.goHome')}}</a>
      </div>
    </div>
    <div v-else-if="showSuccessRecoverPassword">
      <p class="title is-3 has-text-success has-text-centered">{{$t('requestRecover.recoverSentTitle')}}</p>
      <p class="subtitle is-5 has-text-centered">{{$t('requestRecover.textRequestRecover',{userEmail: emailRecover})}}</p>
      <div class="buttons is-centered">
        <a :href="makeUrl('/')" class="button is-primary">{{$t('login.goHome')}}</a>
      </div>
    </div>
    <div class="box" v-else>
      <Login v-if="showLogIn" @showSignUp="switchSignUp" @showRecoverPassword="switchRecoverPassword"/>
      <SignUp v-if="showSignUp" @showLogIn="switchLogIn" @successSignUp="switchSuccessSignUp"/>
      <RequestRecover v-if="showRecoverPassword" @showLogIn="switchLogIn" @successRequestRecover="switchSuccessRequestRecover"/>
    </div>
  </section>
</template>

<script>
import Login from '../components/auth/Login.vue'
import SignUp from '../components/auth/SignUp.vue'
import RequestRecover from '../components/auth/RequestRecover.vue'

export default {
  name: 'Auth',
  data(){
    return {
      initialized: false,
      fetchingUser: true,
      loggedIn: false,
      showLogIn: true,
      showSignUp: false,
      showRecoverPassword: false,
      showSuccessSignUp: false,
      showSuccessRecoverPassword: false,
      emailSignUp: null,
      emailRecover: null
    }
  },
  components: {
    Login,
    SignUp,
    RequestRecover
  },
  beforeCreate: function(){
    this.$http.get('/user/loggedIn')
      .then((res) => {
        if(res.data.loggedIn){
          const urlParams = new URLSearchParams(window.location.search);
          const redirectUrl = urlParams.get('redirect');
          if(redirectUrl){
            window.location.replace(redirectUrl);
          }
          else {
            this.loggedIn = res.data.loggedIn
          }
        }
        this.initialized = true
      })
      .catch((errUser) => {
        console.error(errUser)
      });
    // this.refreshCropper()
  },
  methods: {
    switchSignUp() {
      this.showSignUp = true
      this.showLogIn = false
      this.showRecoverPassword = false
      this.showSuccessSignUp = false
      this.showSuccessRecoverPassword = false
    },
    switchLogIn() {
      this.showSignUp = false
      this.showLogIn = true
      this.showRecoverPassword = false
      this.showSuccessSignUp = false
      this.showSuccessRecoverPassword = false
    },
    switchRecoverPassword() {
      this.showSignUp = false
      this.showLogIn = false
      this.showRecoverPassword = true
      this.showSuccessSignUp = false
      this.showSuccessRecoverPassword = false
    },
    switchSuccessSignUp(email) {
      this.showSignUp = false
      this.showLogIn = false
      this.showRecoverPassword = false
      this.showSuccessSignUp = true
      this.showSuccessRecoverPassword = false
      this.emailSignUp = email
    },
    switchSuccessRequestRecover(email) {
      this.showSignUp = false
      this.showLogIn = false
      this.showRecoverPassword = false
      this.showSuccessSignUp = false
      this.showSuccessRecoverPassword = true
      this.emailRecover = email
    },
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/app.scss";

</style>
