const baseUtils = {
  computed: {
    isSpanish: function () {
			return window.location.pathname.includes('/en/') != 0;
		},
  },
  methods: {
    makeUrl: function(url,redirect){
      let theUrl = null
      if(!this.isSpanish) {
        theUrl = `/en${url}`
        if(redirect) theUrl += `?redirect=/en${redirect}`
      } else {
        theUrl = `${url}`
        if(redirect) theUrl += `?redirect=${redirect}`
      }
      return theUrl
    }
  }
}

export default baseUtils
