<template>
  <section>
    <h1 class="title is-1 has-text-centered">Leaderboard</h1>
		<div v-if="isLeaderboardLoading">
			<br>
			<br>
			<br>
			<h1 class="subtitle title is-5 has-text-centered">Cargando</h1>
			<br>
			<br>
			<br>
		</div>
		<div v-else>
		<div class="buttons is-centered">
			<button @click="sortLeaderboardBy = 'points'" :disabled="sortLeaderboardBy == 'points'" class="button is-warning is-small is-uppercase has-text-weight-bold" >{{$t('ctf.leaderboard.sortByPoints')}}</button>
			<button @click="sortLeaderboardBy = 'solves'" :disabled="sortLeaderboardBy == 'solves'" class="button is-warning is-small is-uppercase has-text-weight-bold" >{{$t('ctf.leaderboard.sortBySolves')}}</button>
		</div>
			<div class="columns is-centered is-variable is-4 is-multiline is-mobile">
				<div class="column is-2-fullhd is-3-desktop is-6-mobile is-4-tablet" v-for="(solver,i) in sortedLeaderboard.slice(0,10)" :key="`main-solvers-${solver.userID}`">
					<CuteBox class="leaderboard-box has-text-centered">
						<img :src="`https://api.furfiesta.com/ctf/avatar/${solver.userID}`" loading="lazy" class="image leaderboard-avatar" />
						<p class="has-text-weight-bold is-size-5 is-marginless"><span class="has-text-warning is-size-3">{{i+1}}°</span><br>{{solver.name}}</p>
						<p class="font-pixel" v-if="sortLeaderboardBy == 'points'">{{nFormatter(solver.points,2)}}</p>
						<p class="font-pixel" v-else-if="sortLeaderboardBy == 'solves'">{{solver.solves.length}} ✔️</p>
					</CuteBox>
				</div>
			</div>
			<div class="columns is-centered">
				<div class="column is-4">
					<CuteBox>
						<table class="table is-fullwidth" style="background-color: transparent; color: #FFF;">
							<tbody>
								<tr v-for="(solver,i) in sortedLeaderboard.slice(0,Math.floor(sortedLeaderboard.length/2))" :key="`solver-${i}`">
									<td width="60%">{{i+1}}. {{solver.name}}</td>
									<td width="40%" style="vertical-align: middle;" class="has-text-right font-pixel" v-if="sortLeaderboardBy == 'points'">{{nFormatter(solver.points,2)}}</td>
									<td width="40%" style="vertical-align: middle;" class="has-text-right font-pixel" v-else-if="sortLeaderboardBy == 'solves'">{{solver.solves.length}} ✔️</td>
								</tr>
							</tbody>
						</table>
					</CuteBox>
				</div>
				<div class="column is-4">
					<CuteBox>
						<table class="table is-fullwidth" style="background-color: transparent; color: #FFF;">
							<tbody>
								<tr v-for="(solver,i) in sortedLeaderboard.slice(Math.floor(sortedLeaderboard.length/2),sortedLeaderboard.length)" :key="`solver-${i}`">
									<td width="60%">{{Math.floor(sortedLeaderboard.length/2)+1+i}}. {{solver.name}}</td>
									<td width="40%" style="vertical-align: middle;" class="has-text-right font-pixel" v-if="sortLeaderboardBy == 'points'">{{nFormatter(solver.points,2)}}</td>
									<td width="40%" style="vertical-align: middle;" class="has-text-right font-pixel" v-else-if="sortLeaderboardBy == 'solves'">{{solver.solves.length}} ✔️</td>
								</tr>
							</tbody>
						</table>
					</CuteBox>
				</div>
			</div>
		</div>
  </section>
</template>

<script>
import CuteBox from './CuteBox.vue'
export default {
  data() {
    return {
			leaderboard: [],
			isLeaderboardLoading: true,
			idIntervalLeaderboard: null,
			sortLeaderboardBy: 'points'
    }
  },
  components: {
    CuteBox
  },
  computed: {
    sortedLeaderboard: function () {
			if(!this.leaderboard.length) return []
			if(this.sortLeaderboardBy == 'points'){
				return [...this.leaderboard].sort( (a,b) => b.points - a.points)
			} else {
			return [...this.leaderboard].sort( (a,b) => b.solves.length - a.solves.length || b.points - a.points)
			}
		}
  },
  mounted: function() {
    this.refreshLeaderboard()
		this.idIntervalLeaderboard = setInterval(this.refreshLeaderboard, 15000);
  },
  beforeDestroy: function(){
    clearInterval(this.idIntervalLeaderboard)
  },
  methods: {
    refreshLeaderboard: function(){
    console.info('[CTF] [Leaderboard] Refreshing...')
    this.$http.get('/ctf/leaderboard')
      .then(res => {
        this.isLeaderboardLoading = false
        this.leaderboard = res.data
      })
    },
    nFormatter: function (num, digits) {
		const lookup = [
			{ value: 1, symbol: "" },
      { value: 1e3, symbol: "k" },
      { value: 1e6, symbol: "M" },
      { value: 1e9, symbol: "G" },
      { value: 1e12, symbol: "T" },
      { value: 1e15, symbol: "P" },
      { value: 1e18, symbol: "E" }
    ];
    const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
    var item = lookup.slice().reverse().find(function(item) {
      return num >= item.value;
    });
    return item ? (num / item.value).toFixed(digits).replace(rx, "$1") + item.symbol : "0";
	},
  }
}
</script>

