<template>
  <div>
    <h1 class="title has-text-centered">{{$t('signup.title')}}</h1>
    <h1 class="subtitle has-text-centered">{{$t('signup.subtitle')}}</h1>
    <hr>
    <h1 class="title is-4 is-uppercase has-text-centered has-text-danger is-700">{{$t('signup.requiredFields')}}</h1>
    <hr>
    <form>
          <div class="field">
            <p class="control">
              <label class="label">{{$t('signup.labelEmail')}}</label>
              <input required class="input" v-model="newUser.email" type="email" :placeholder="$t('signup.placeholderEmail')">
            </p>
          </div>
            <div class="columns">
              <div class="column">
                <label class="label">{{$t('signup.labelPassword')}}</label>
                <div class="control">
                  <input required class="input" v-model="newUser.password" type="password" :placeholder="$t('signup.placeholderPassword')">
                  <p class="help">{{$t('signup.helpTextPassword')}}</p>
                </div>
              </div>
              <div class="column">
                <label class="label">{{$t('signup.labelRepeatPassword')}}</label>
                <div class="control">
                  <input required class="input" v-model="newUser.repeatPassword" type="password" :placeholder="$t('signup.placeholderRepeatPassword')">
                  <p class="help">{{$t('signup.helpTextRepeatPassword')}}</p>
                </div>
              </div>
            </div>
          <div class="columns">
                <div class="column">
                  <div class="field">
                   <label class="label">{{$t('signup.labelRealNames')}}</label>
                   <div class="control">
                     <input class="input" v-model="newUser.names" required type="text" :placeholder="$t('signup.placeholderRealNames')">
                   </div>
                  </div>
                </div>
                <div class="column">
<div class="field">
           <label class="label">{{$t('signup.labelRealSurnames')}}</label>
           <div class="control">
             <input class="input" v-model="newUser.surnames" required type="text" :placeholder="$t('signup.placeholderRealSurnames')">
           </div>
          </div>
                </div>
            <div class="column">
			<div class="field">
              <label class="label">{{$t('signup.labelCountry')}}</label>
              <div class="control">
                <div class="select is-fullwidth">
                  <select required v-model="newUser.country" >
                    <option :value="null" disabled>{{$t('signup.placeholderCountry')}}</option>
                    <option v-for="(v,k) in $t('countries')" :key="k" :value="k">{{v}}</option>
                  </select>
                </div>
              </div>
            </div>
            </div>
          </div>
            <div class="columns">
                <div class="column">
                  <div class="field">
                   <label class="label">{{$t('signup.labelFursonaFullname')}}</label>
                   <div class="control">
                     <input class="input" v-model="newUser.fursonaFullname" required type="text" :placeholder="$t('signup.placeholderFursonaFullname')">
                   </div>
                  </div>
              </div>
              <div class="column">
                <div class="field">
                  <label class="label">{{$t('signup.labelFursonaDisplayName')}}</label>
                  <div class="control">
                    <input class="input" v-model="newUser.fursonaDisplayName" required type="text" :placeholder="$t('signup.placeholderFursonaDisplayName')">
                  </div>
                </div>
              </div>
            </div>
             <div class="columns">
                <div class="column">
                   <div class="field">
                    <label class="label">{{$t('signup.labelFursonaSpecies')}}</label>
                    <div class="control">
                      <input class="input" v-model="newUser.fursonaSpecies" required type="text" :placeholder="$t('signup.placeholderFursonaSpecies')">
                      <p class="help">{{$t('signup.helpTextSpecies')}}</p>
                    </div>
                  </div>
               </div>
                <div class="column">
                   <div class="field">
            <label class="label">{{$t('signup.labelPronouns')}}</label>
            <div class="control">
              <input class="input" v-model="newUser.fursonaPronoun" required type="text" :placeholder="$t('signup.placeholderPronoun')">
              <p class="help">{{$t('signup.helpTextPronoun')}}</p>
            </div>
          </div>
               </div>
            </div>
           <div class="notification is-warning" v-if="signUpEmailFailed">
            <span>{{$t('signup.textSignUpFail')}}</span>
          </div>
          <div class="buttons is-right">
            <button @click.prevent="doShowLogin" class="button is-white">{{$t('signup.goBack')}}</button>
            <captcha sitekey="6Lf4T9IaAAAAAE2FUUkD39CTR9-rmTOomRStaqcq" :validate="checkSignUp" :callback="doSignUp"
            class="button is-primary" :class="{'is-loading': signUpEmailLoading}" type="submit" id="register-button" :disabled="signUpEmailLoading">
            {{$t('signup.registerButton')}}</captcha>
          </div>
          </form>

  </div>
</template>

<script>
import countriesUtils from '../../mixins/countriesUtils'
import Captcha from './Captcha.vue'
export default {
  data() {
    return {
      newUser: {
        names: null,
        surnames: null,
        password: null,
        repeatPassword: null,
        email: null,
        country: null,
        fursonaFullname: null,
        fursonaDisplayName: null,
        fursonaSpecies: null,
        fursonaPronoun: null
      },
      signUpEmailLoading: false,
      signUpEmailFailed: false
    }
  },
  mixins: [countriesUtils],
  components: {
    Captcha
  },
  computed: {
    isSpanish: function () {
      return window.location.pathname.indexOf('/en/') != 0;
    },
  },
  methods: {
    checkSignUp: function(){
      const valueIsRequired = (val) => val != null;
      const valueIsNotEmptyString = (val) => val != '';
      const isValidEmail = (val) => {
        const re = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,253}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,253}[a-zA-Z0-9])?)*$/
        return re.test(val)
      }

      let valuesSignUp = [
        this.newUser.names,
        this.newUser.surnames,
        this.newUser.email,
        this.newUser.country,
        this.newUser.password,
        this.newUser.repeatPassword,
        this.newUser.fursonaFullname,
        this.newUser.fursonaDisplayName,
        this.newUser.fursonaSpecies,
        this.newUser.fursonaPronoun
      ]

      if (!valuesSignUp.every(valueIsRequired) || !valuesSignUp.every(valueIsNotEmptyString)){
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('signup.errors.allFields')
        })
        return false
      }
      
      if (!isValidEmail(this.newUser.email.toLowerCase().trim())){
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('signup.errors.emailNotValid')
        })
        return false
      }

      if (this.newUser.password.length < 6) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('signup.errors.passwordLengthMin')
        })
        return false
      }

      if (this.newUser.password != this.newUser.repeatPassword) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('signup.errors.passwordDontMatch')
        })
        return false
      }

      return true
    },
    doShowLogin: function () {
      this.$emit('showLogIn')
    },
    doSignUp: function (recaptchaToken) {
      if (!recaptchaToken) {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: this.$t('signup.alertRecaptchaTextFail')
        })
        return
      }
      this.signUpEmailLoading = true
      this.signUpEmailFailed = false
      this.$http.post('/user/register', {
        language: this.isSpanish ? 'es' : 'en',
        names: this.newUser.names,
        surnames: this.newUser.surnames,
        email: this.newUser.email.toLowerCase().trim(),
        country: this.newUser.country,
        password: this.newUser.password,
        fursonaFullname: this.newUser.fursonaFullname,
        fursonaDisplayName: this.newUser.fursonaDisplayName,
        fursonaSpecies: this.newUser.fursonaSpecies,
        fursonaPronoun: this.newUser.fursonaPronoun,
        captcha: recaptchaToken
      })
        .then(() => {
          this.$emit('successSignUp', this.newUser.email.toLowerCase().trim())
          this.$swal.fire({
            icon: 'success',
            title: this.$t('signup.alertSuccessTitle'),
            text: this.$t('signup.textSignUpSuccess', {userEmail: this.newUser.email.toLowerCase().trim()})
          })
        })
        .catch(() => {
          this.$swal.fire({
            icon: 'error',
            title: 'Woops...',
            text: this.$t('signup.textSignUpFail')
          })
        })
        .finally(() => {
          this.signUpEmailLoading = false
        });
    },
  }
}
</script>